@import "../assets/Styles.scss";

.container {
  background-color: black;
  height: calc(100% - #{$settings-height});
  min-height: 300px;
  display: flex;
  justify-content: center;

  .chart {
    width: calc(100vw - 40px);
    display: flex;
    align-items: flex-end;
    justify-content: space-between;
  }
}
